import { GridEditorManager } from "../../grid/editing/grid-editor-manager";
import { TreeListSelectors } from "../tree-list-selectors";
export class TreeListEditorManager extends GridEditorManager {
    constructor(grid) {
        super(grid);
    }
    isDataCellInteractiveElement(element) {
        if (!element)
            return false;
        return element.matches(TreeListSelectors.TreeNodeExpandControlsCell) || element.matches(TreeListSelectors.TreeNodeExpandButton);
    }
}
