import { KeyCode, KeyUtils } from "@devexpress/utils/lib/utils/key";
export class TreeListKbdClientHelper {
    static isExpandCollapseHotkey(evt, isMacOsPlatform) {
        const keyCode = KeyUtils.getEventKeyCode(evt);
        if (keyCode !== KeyCode.Right && keyCode !== KeyCode.Left)
            return false;
        return isMacOsPlatform ? evt.metaKey : evt.ctrlKey;
    }
}
